// Skip Navi
.skipNavi {
	positon: absolute;
	top: 0;
	left: 0;
	width: 100%;
	a {
		display: block;
		background: red;
		color: white;
		font: {
			size: 0; }
		&:focus {
			font: {
				size: 14px; } } } }
#wrap {}

/* header */
$header-color       : $color-1;

#header {
	border: {
		bottom: 1px solid $border-color; }
	padding: 0 $gap;
	@include desktop {
		padding: 0; }
	h1 {
		a {
			display: flex;
			vertical-align: top;
			padding: 31px 0 29px;
			align-items: center; }
		span {
			display: inline-flex;
			margin: {
				right: 8px; }
			img {
				width: 75px;
				height: 21px; } }
		strong {
			display: inline-flex;
			align-items: center;
			@include font-set(14px, -.42px, 22px);
			color: $header-color;
			font: {
				weight: $weight-normal; } } } }

