.is-mobile-inner {
	padding: {
		left: $gap;
		right: $gap; }
	overflow: hidden;
	@include desktop {
		padding: {
			left: 0;
			right: 0; } } }


// Bi-box
$bi-box-border-color: $color-11;

.bi-box {
	margin: {
		top: 80px; }
	padding: {
		bottom: 80px; }
	@include mobile {
		margin: {
			top: 60px; } }
	.container {
		padding: {
			top: 20px; }
		border: {
			top: 1px solid $bi-box-border-color; } }

	.bi-list {
		display: flex;
		justify-content: center;
		padding: 0 77px;
		flex-wrap: wrap;
		// 2021-06-08 13:06:21
		max-width: 840px;
		margin: {
			left: auto;
			right: auto; }
		@include mobile {
			padding: 0; }
		@include touch {
			max-width: 810px; }
		li {
			display: flex;
			padding: 0 6px;
			position: relative;
			&::before {
				content: '';
				width: 1px;
				height: 14px;
				position: absolute;
				top: 50%;
				right: 0;
				background: {
					color: $bi-box-border-color; }
				transform: translateY(-50%); }
			&:nth-child(5) {
				&::before {
					display: none; } }
			@include touch {
				&:nth-child(4) {
					&::before {
						display: none; } }
				&:nth-child(5) {
					&::before {
						display: block; } } }
			&:last-child {
				&::before {
					display: none; } }
			img {
				width: 120px;
				max-width: none; } }
		@include mobile {
			li {
				width: 25%;
				img {
					width: 100%; } } } } }


