/* Content Common */

/*   */
$text-list-color: $color-6;

%list-style {
	@include font-set(13px, -.26px, 21px);
	color: $text-list-color;
	text-align: left; }

.titleList {
	dl {
		dt {
			@extend %list-style;
			margin: {
				bottom: 0; } }
		dd {
			@extend %list-style;
			&.list {
				margin: {
					top: 0px; } } } } }
.list {
	li {
		@extend %list-style; }
	&.hyphen {
		li {
			text-indent: -5px;
			padding: {
				left: 5px; }
			&::before {
				content: '- '; } } }
	&.dotted {
		li {
			padding: {
				left: 7px; }
			position: relative;
			+ li {
				margin: {
					top: 5px; } }
			&::before {
				content: '';
				width: 3px;
				height: 3px;
				background: {
					color: $text-list-color; }
				position: absolute;
				top: 8px;
				left: 0; } } }


	.is-text {
		@extend %list-style;
		display: inline; } }

/* Point Color */
$point-color-1           : $color-2;
$point-color-2           : $color-3;
$point-color-3           : $color-1;

.point-color-1 {
	color: $point-color-1 !important; }
.point-color-2 {
	color: $point-color-2 !important; }
.point-color-3 {
	color: $point-color-3 !important; }

/* Align */
$aligns: left center right;
@each $align in $aligns {
	.is-align-#{$align} {
		text-align: #{$align}; } }

/* Style */
.underline {
	text-decoration: underline !important; }

/* Border */
$border-position: top bottom left right;
$border-color   : $color-11;

@each $position in $border-position {
	.is-border-#{$position} {
		border-#{$position}: 1px solid $border-color; } }

/*  */
$title-point-color: $color-2;
.title-field {
	border: {
		bottom: 1px solid $border-color; }
	padding: {
		bottom: 30px;
		left: $gap;
		right: $gap; }
	@include desktop {
		padding: {
			left: 0px;
			right: 0px; } }
	.title {
		&:not(:last-child) {
			margin: {
				bottom: 15px; } } }
	p {
		@include font-set(15px, -.3px, 23px);
		+ p {
			margin: {
				top: 4px; }
			@include font-set(13px, -.26px, 21px);
			color: $title-point-color; } }
	&.type-2 {
		text-align: center; }
	&.type-3 {
		border: {
			bottom: none; }
		margin: {
			top: 50px; } } }

.title {
	&.is-2 {
		line-height: 38px;
		letter-spacing: -1.2px; } }

/* Search */
.is-search {
	.input {
		padding: {
			right: 60px; } }
	.btn-search {
		width: 60px;
		height: 50px;
		position: absolute;
		top: 0;
		right: 0;
		background: {
			color: transparent;
			image: url(#{$image-path}btn_search.svg);
			repeat: no-repeat;
			position: center; }
		border: none;
		font: {
			size: 0; }
		cursor: pointer;
		&:focus {
			outline: none;
			border: none; } } }

/* No Data */
$no-data-font-color: $color-6;
.no-data {
	justify-content: center;
	display: flex;
	align-items: center;
	flex-grow: 1;
	p {
		@include font-set(18px, -.36px, 26px);
		color: $no-data-font-color;
		padding: {
			top: 75px; }
		background: {
			image: url(#{$image-path}ic_fail.svg);
			repeat: no-repeat;
			position: center top; } } }

/* validation */
$field-message-color: $color-6;
$error-color        : $color-2;
$success-color      : $color-3;

@mixin valid-icon($color) {
	+ .msg {
		position: relative;
		padding: {
			left: 24px; }
		color: $color;
		// PG_NP_4004_1.html  
		+ .columns {
			margin: {
				top: calc(10px - #{$column-gap}); } }
		&::before {
			content: '!';
			border: 1px solid $color;
			border: {
				radius: 100%; }
			width: 16px;
			height: 16px;
			position: absolute;
			top: 3px;
			left: 0;
			text-align: center;
			line-height: 14px; } } }

.columns {
	&.is-error {
		.input,
		.select select {
			border: {
				color: $error-color; } }
		@include valid-icon($error-color); }
	&.is-success {
		@include valid-icon($success-color); } }

.msg {
	margin: {
		top: calc(10px - #{$column-gap}); }
	font: {
		size: 13px; }
	letter-spacing: -.26px;
	line-height: 21px;
	color: $field-message-color; }


