.checkbox {
	&.agree-checkbox {
		> input {}
		> i {
			border: none;
			top: 0;
			left: 0;
			transform: translateY(0px);
			&::before {
				width: 12px;
				height: 8px;
				border: 2px solid $agree-checkbox-base-color;
				border: {
					right: 0;
					top: 0; }
				top: 6px;
				left: 6px;
				transform: rotate(-45deg);
				transition: all .5s; } }
		> input {
			&:checked {
				+ i {
					background: {
						color: transparent; }
					border: none;
					&:before {
						border: {
							color: $agree-checkbox-current-color; } } } } }
		.agree-text-box {
			display: block;
			.label {
				display: block;
				@include font-set(15px, -.3px, 23px);
				font: {
					weight: $weight-normal; }
