			.discription {
				display: block;
				margin: {
					top: 7px; }
				@include font-set(15px, -.3px, 23px);
				color: $agree-checkbox-label-color;
				text-indent: -5px;
				padding-left: 5px;
				font: {
					weight: $weight-medium; }
				&.type-2 {
					@include font-set(13px, -.26px, 21px);
					font: {
						weight: $weight-normal; } }
				&::before {
					content: '- '; }
				strong {
					font: {
						weight: $weight-normal; }
					display: inline; } } }
