/* step */
$step-box-background-color            : $color-15;
$step-box-icon-background-color       : $color-8;
$step-box-icon-active-check-color     : $color-8;
$step-box-icon-active-background-color: $color-3;
$step-box-icon-active-border-color    : $color-3;
$step-box-icon-active-color           : $color-1;
$step-box-icon-complete-color         : $color-3;
$step-box-complete-color              : $color-4;
$step-box-icon-color                  : $color-7;

$step-box-line-color                  : $color-11;

.step-box {
	background: {
		color: $step-box-background-color; }
	display: flex;
	padding: 16px 0;
	align-items: center;
	justify-content: center;
	ol {
		display: flex;
		align-items: center;
		justify-content: space-around;
		position: relative;
		&::before {
			content: '';
			width: calc(100% - 40px);
			height: 1px;
			position: absolute;
			top: 13px;
			left: 50%;
			transform: translateX(-50%);
			// border:
			// 	top: 1px dotted $step-box-line-color
			background: {
				color: $step-box-line-color; } }
		&::after {
			content: '';
			width: calc(100% - 40px);
			height: 1px;
			position: absolute;
			top: 13px;
			left: 50%;
			transform: translateX(-50%); }
		&[data-step="4"] {
			@for $i from 1 through 4 {
				&.step-#{$i} {
					&::after {
						background: linear-gradient(to right, bulmaRgba($step-box-icon-active-border-color, 1) 0%, bulmaRgba($step-box-icon-active-border-color, 1) #{($i - 1) * 33.3%}, bulmaRgba($border-color, 1) #{($i - 1) * 33.3%}, bulmaRgba($border-color, 1) 100%); } } } }
		&[data-step="3"] {
			@for $i from 1 through 3 {
				&.step-#{$i} {
					&::after {
						background: linear-gradient(to right, bulmaRgba($step-box-icon-active-border-color, 1) 0%, bulmaRgba($step-box-icon-active-border-color, 1) #{($i - 1) * 50%}, bulmaRgba($border-color, 1) #{($i - 1) * 50%}, bulmaRgba($border-color, 1) 100%); } } } } }
	li {
		list-style: none;
		// display: flex
		margin: {
			left: 40px; }
		@media screen and ( max-width: 320px ) {
			margin: {
				left: 20px; } }

		position: relative;
		z-index: 1;
		i {
			display: block;
			margin: auto;
			width: 26px;
			height: 26px;
			border: 1px solid $border-color;
			border-radius: 100%;
			font: {
				style: normal; }
			text-align: center;
			padding: 2px 0 4px;
			@include font-set(14px, -.28px, 21px);
			color: $step-box-icon-color;
			background: {
				color: $step-box-icon-background-color; }
			span {
				font: {
					size: 0; } } }
		strong {
			display: block;
			flex: {
				direction: column; }
			color: $step-box-icon-color;
			@include font-set(13px, -.26px, 23px);
			margin: {
				top: 3px; } }
		&:first-child {
			margin: {
				left: 0px; } }
		&.is-active {
			i {
				font: {
					size: 0; }
				background: {
					color: $step-box-icon-active-background-color; }
				border: {
					color: $step-box-icon-active-border-color; }
				position: relative;
				&::before {
					content: '';
					border: 2px solid $step-box-icon-active-check-color;
					border-right: 0;
					border-top: 0;
					position: absolute;
					top: 6px;
					left: 6px;
					transform: rotate(-45deg);
					width: 12px;
					height: 8px; } }
			strong {
				font: {
					weight: $weight-medium; }
				color: $step-box-icon-active-color; } }
		&.is-complete {
			i {
				border: {
					color: $step-box-icon-active-border-color; }
				color: $step-box-icon-complete-color;
				font: {
					weight: $weight-medium; } }
			strong {
				color: $step-box-complete-color; } } } }

