// Loading
$loading-box-background-color: rgba($color-14, 0.1);
.loading-box {
	width: 100%;
	height: 100vh;
	position: fixed;
	top: 0;
	left : 0;
	background: {
		color: $loading-box-background-color; }
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 100;

	%loading-animation {
		border-radius: 50%;
		width: 1.800em;
		height: 1.800em;
		-webkit-animation-fill-mode: both;
		animation-fill-mode: both;
		-webkit-animation: load7 1.8s infinite ease-in-out;
		animation: load7 1.8s infinite ease-in-out; }
	&.is-transparent {
		background: {
			color: transparent; } }
	.loading {
		@extend %loading-animation;

		color: #FF9E1E;
		font-size: 10px;
		position: relative;
		text-indent: -9999em;
		-webkit-transform: translateZ(0);
		-ms-transform: translateZ(0);
		transform: translateZ(0);
		-webkit-animation-delay: -0.16s;
		animation-delay: -0.16s;
		text-align:center {}
		&::before {
			@extend %loading-animation;

			content: '';
			position: absolute;
			top: 0;
			left: -3.000em;
			-webkit-animation-delay: -0.32s;
			animation-delay: -0.32s; }
		&::after {
			@extend %loading-animation;

			content: '';
			position: absolute;
			top: 0;
			left: 3.000em; } } }

@keyframes load7 {
	0%,
	80%,
	100% {
		box-shadow: 0 3em 0 -2em; }
	40% {
		box-shadow: 0 3em 0 0; } }




